<template>
  <div id="app" class="shadow-lg p-4">
    <h1 class="font-bold">Kapiva - Add to cart link generator</h1>
    <div v-if="!products">Loading products...</div>
    <div v-if="products">
      <div class="mb-4 text-xs">
        There are {{ products.length }} products loaded
      </div>

      <div
        class="no-items text-gray-400 border-y py-4"
        v-if="listItems.length == 0"
      >
        You have not added any items.
      </div>

      <div class="list-items mb-4 py-4 border-y" v-if="listItems.length > 0">
        <div class="item flex mb-2" v-for="item in listItems" :key="item.id">
          <v-select
            class="shadow appearance-none border rounded flex-1 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
            :options="products"
            label="title"
            :disabled="true"
            :clearable="false"
            :searchable="false"
            placeholder="Select Product"
            v-model="item.product"
          ></v-select>
          <input
            class="shadow appearance-none border rounded w-16 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline ml-4"
            placeholder="Qty."
            v-model="item.qty"
            type="number"
            min="1"
          />
          <button
            type="button"
            class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded ml-4"
            @click="deleteFromListItems(item.product.id)"
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              class="h-6 w-6"
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              stroke-width="2"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
              />
            </svg>
          </button>
        </div>
      </div>

      <form class="flex mb-4 py-4 border-b" @submit.prevent="onAddSubmit">
        <v-select
          class="shadow appearance-none border rounded flex-1 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
          :options="selectableProducts"
          label="title"
          placeholder="Select Product"
          v-model="addItem.product"
          required
        >
          <template #search="{ attributes, events }">
            <!-- https://vue-select.org/guide/validation.html#required -->
            <input
              class="vs__search"
              :required="!addItem.product"
              v-bind="attributes"
              v-on="events"
            />
          </template>
        </v-select>
        <input
          class="shadow appearance-none border rounded w-16 py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline ml-4"
          placeholder="Qty."
          v-model="addItem.qty"
          type="number"
          min="1"
        />
        <button
          type="submit"
          class="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded ml-4"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-6 w-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            stroke-width="2"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              d="M12 4v16m8-8H4"
            />
          </svg>
        </button>
      </form>

      <div class="flex gap-4 extra-params mb-4 pb-4 border-b">
        <input
          class="shadow appearance-none border rounded flex-1 w-1/2 py-2 px-4 h-12 bg-blue-50 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
          placeholder="UTM Source"
          v-model="extraParams.utm_source"
          type="text"
          min="1"
        />
        <input
          class="shadow appearance-none border rounded flex-1 w-1/2 py-2 px-4 h-12 bg-blue-50 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
          placeholder="Coupon"
          v-model="extraParams.coupon"
          type="text"
          min="1"
        />
      </div>

      <div class="flex max-w-full">
        <input
          class="shadow appearance-none border rounded-l flex-1 py-2 px-4 h-12 bg-blue-50 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
          placeholder="Add to cart link will appear here"
          readonly
          :value="atcLink"
          type="text"
          min="1"
        />
        <button
          type="button"
          class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-r"
          @click="copy(atcLink)"
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            class="h-8 w-8"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            stroke-width="2"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2"
            />
          </svg>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import "vue-select/dist/vue-select.css";

import axios from "axios";
import vSelect from "vue-select";
import copy from "copy-to-clipboard";

export default {
  name: "App",
  components: {
    "v-select": vSelect,
  },
  data: function () {
    return {
      products: [],
      listItems: [],
      addItem: {
        product: null,
        qty: 1,
      },
      extraParams: {
        utm_source: "",
        coupon: "",
      },
    };
  },
  computed: {
    selectableProducts() {
      if (this.listItems.length < 1) {
        return this.products;
      }

      return this.products.filter((o) => {
        return !this.listItems.map((x) => x.product.id).includes(o.id);
      });
    },
    atcLink() {
      var params = {};
      if (this.listItems.length > 0) {
        params.atc = this.listItems
          .map((o) => {
            return "" + o.product.id + (o.qty > 1 ? ":" + o.qty : "");
          })
          .join(",");
      }
      var url = new URL("https://staging.kapiva.in/checkout-custom/");
      url.search = [
        ...Object.keys(params).map((o) => o + "=" + params[o]),
        ...Object.keys(this.extraParams)
          .filter((k) => this.extraParams[k])
          .map((o) => o + "=" + this.extraParams[o]),
      ].join("&");

      // console.log(url.toString());
      return url.toString();
    },
  },
  methods: {
    async fetchProducts() {
      const resp = await axios.get(
        "https://product-feeds.kapiva.in/products-staging.php"
      );
      this.products = resp.data;
      // console.log(resp.data);
    },
    resetAddItem() {
      this.addItem.product = null;
      this.addItem.qty = 1;
    },
    onAddSubmit() {
      this.listItems.push({
        product: this.addItem.product,
        qty: this.addItem.qty,
      });
      this.resetAddItem();
    },
    deleteFromListItems(id) {
      const idx = this.listItems.map((o) => o.product.id).indexOf(id);
      if (idx !== -1) {
        this.listItems.splice(idx, 1);
      }
    },
    copy(data) {
      copy(data); // (x.x)
    },
  },
  created() {
    this.fetchProducts();
  },
};
</script>

